export default "#define GLSLIFY 1\nuniform sampler2D uTex;\nuniform float uVisibility;\nvarying vec2 vUv;\n\nvoid main( void ) {\n\n\tvec4 col = texture2D( uTex, vUv );\n\n\tcol.w *= step( abs ( vUv.y - 0.5 ), uVisibility * 0.5 );\n\n\tif( col.w < 0.5 ) {\n\n\t\tdiscard;\n\t\t\n\t}\n\n\tgl_FragColor = col;\n\n}";