export default "#define GLSLIFY 1\nuniform float uVisibility;\nvarying vec2 vUv;\n\nvoid main( void ) {\n\n\tvec3 col = vec3( 0.0 );\n\n\tvec2 glid = step( vec2( 0.985 ), mod( vUv * 20.0, vec2( 1.0, 1.0 ) ) );\n\n\tcol += min( 1.0, glid.x + glid.y ) * 0.2;\n\tcol *= uVisibility;\n\n\tgl_FragColor = vec4( col, 1.0 );\n\n}";